/* tslint:disable max-line-length*/
import { Injectable } from "@angular/core";
import { ApplicationConstants } from "../../config/application-constants.config";
import { HttpService } from "../utils/http.service";

@Injectable()
export class InfiniteScroll {
    readonly PARAM_KEY_START: string = "start";
    readonly PARAM_KEY_PAGE: string = "page";

    private url: string;
    private params: any;
    private options: any;
    private currentpage: number;
    private total: number;
    private results: any = {};

    constructor(private httpservice: HttpService) { }

    public init(url: string, params: any, results: any, options?: any) {
        this.loadDefault();
        this.url = url;
        this.params = params;
        this.results = results;
        if (options) {
            if (options.hasOwnProperty("pageSize")) {
                this.options.pageSize = options.pageSize;
            }
            if (options.hasOwnProperty("keyData")) {
                this.options.keyData = options.keyData;
            }
            if (options.hasOwnProperty("keyTotal")) {
                this.options.keyTotal = options.keyTotal;
            }
        }
    }

    public loadInitialData(): Promise<any> {
        const initialData = this.httpservice.get(this.url, this.params).map((response) => {
            if (response) {
                this.currentpage = 1;
                this.total = response[this.options.keyTotal];
                return response.hasOwnProperty(this.options.keyData) ? response[this.options.keyData] : {};
            }
        });
        return initialData.toPromise().then((results) => {
            return this.results.push(...results);
        }).catch((err) => {
            return err;
        });
    }

    public loadMoreData(event: any) {
        if (this.total) {
            if (this.total > (this.currentpage * this.options.pageSize)) {
                this.params[this.PARAM_KEY_START] = this.currentpage * this.options.pageSize;
                this.currentpage += 1;
                this.params[this.PARAM_KEY_PAGE] = this.currentpage;
                this.httpservice.get(this.url, this.params).map((response) => {
                    event.complete();
                    // event.enable(this.total > this.currentpage * this.options.pageSize);
                    return response[this.options.keyData];
                }).subscribe((results) => {
                    this.results.push(...results);
                });
            } else {
                event.complete();
                // event.enable(false);
            }
        }
    }

    private loadDefault() {
        this.url = null;
        this.params = null;
        this.results = null;
        this.options = { pageSize: 25, keyData: "data", keyTotal: "total"};
    }

}



