import {
  Injectable,
} from "@angular/core";
import "rxjs/add/operator/map";

import {
  Config,
} from "../config/service-url.config";
import {
  HttpService,
} from "../common/utils/http.service";
import {
  Session,
} from "../common/model/session";
import MomentTz from "moment-timezone";
import moment from "moment";

@Injectable()
export class OutLookService {

  constructor(public http: HttpService, public session: Session) {}

  public addScheduleAppoinment(appointmentRequest) {
    // moment
    const user = this.session.get("currentUser").username;
    const outlookAppoinment = {
      userId: user,
      subject: appointmentRequest.description,
      content: appointmentRequest.comment,
      startDateTime: appointmentRequest.startDateTime,
      endDateTime: appointmentRequest.endDateTime,
      isOutlookIntegration: "true",
    };

    this.addAppoinment(outlookAppoinment);
  }
  public addEventAppoinment(eventRequest) {
    const user = this.session.get("currentUser").username;
    const outlookAppoinment = {
      userId: user,
      subject: eventRequest.dateTypeId + " - " + eventRequest.contactId,
      content: eventRequest.comment,
      startDateTime: eventRequest.dateValue,
      EndDateTime: eventRequest.dateValue,
      isAllDayEvent: "true",
      isOutlookIntegration: "true",
    };
    this.addAppoinment(outlookAppoinment);
  }
  public addAppoinment(outlookAppoinment) {
    const user = this.session.get("currentUser").username;
    outlookAppoinment.userId = user;

    this.http.post(Config.INTEGRATION_OUTLOOK_APPOINMENT,
        outlookAppoinment)
      .subscribe((response) => {
        if (response && response.status) {
          // console.log(response.status)
        }
      });
    // .catch((error) => {
    //   // console.log('Error : ', error)
    // });
  }
}



