import { Injectable } from "@angular/core";
import { Http } from "@angular/http";
import "rxjs/add/operator/map";
import { HttpService } from "../common/utils/http.service";
import { Filters } from "../common/utils/filters";
import { Config } from "../config/service-url.config";
import { ApplicationConstants } from "../config/application-constants.config";

/*
  Generated class for the ContactService provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular 2 DI.
*/
@Injectable()
export class ContactService {

  cache: any = {
    contacts: "",
    total: "",
  };
  searchobj: any = {
    searchparams: {},
    searchtotal: "",
    currentpage: "",
  };
  constructor(public httpservice: HttpService, public filters: Filters) {
  }

  makeParam(start, limit) {
    const param = {start, limit };
    return param;
  }

  // Private method to create list of params
  getParamList() {
    // Hardcoding , this needs to be moved to constants
    const limit = 25;
    // tslint:disable-next-line:prefer-const
    let total = this.cache.total;
    const paramList = [];
    let start = 0;
    while (start <= total) {
      paramList.push(this.makeParam(start, limit));
      start = start + limit - 1;
    }
    return paramList;
  }

  /*
   * Returns the promise of get contacts list api
   * Input param object with start and limit
   * param{
   *  start: 20,
   *  limit: 20
   * }
   */
  getContactsPromise(param) {
    return this.httpservice.get(Config.CONTACT_LIST_URL, param).map((response) => {
      return (response.data);
    });
  }

 /*
  * Method to fetch contacts
  * Returns an array of contact chunks
  * eg: [chunk1,chunk2...]
  * where chunk1 is an array containing contacts and so on..
  */
  // getContacts() {
  //   const paramList = this.getParamList();
  //   return (paramList.map((param) => this.getContactsPromise(param) ));
  // }

  public searchContacts(searchString) {
    const params = {
      advSearchYn: true,
      page: 1,
      start: 0,
      limit: 25,
      name: null,
      ssn: null,
      phone: null,
    };

    if (Number(searchString)) {
      //  if (searchString.length === ApplicationConstants.SSN_DIGIT) {
      //    params.ssn = Filters.formatSSN(searchString);
      //  }

      if (searchString.length === ApplicationConstants.PHONENUMBER_DIGIT) {
        params.phone = Filters.formatPhoneNumber(searchString);
      }
    } else {
      params.name = searchString;
    }

    this.searchobj.searchparams = params;
    return this.httpservice.get(
      Config.CONTACT_FLEX_FIND,
      this.searchobj.searchparams,
    ).map((response) => {
      this.searchobj.searchTotal = response.total;
      this.searchobj.currentpage = 1;
      return response;
    });
    // handle errors
    // .catch(() => {#<{(| do something here |)}>#});
  }

  getNextPage() {
    if (this.searchobj.searchtotal) {
      // tslint:disable-next-line:max-line-length
      if (this.searchobj.searchtotal > (this.searchobj.currentpage * ApplicationConstants.RESPONSE_DATA_LIMIT)) {
        // tslint:disable-next-line:max-line-length
        // tslint:disable-next-line:no-string-literal
        this.searchobj.searchparams["start"] = this.searchobj.currentpage * ApplicationConstants.RESPONSE_DATA_LIMIT;
        this.searchobj.currentpage += 1;
        // tslint:disable-next-line:no-string-literal
        this.searchobj.searchparams["page"] = this.searchobj.currentpage;
        return this.httpservice.get(Config.CONTACT_FLEX_FIND, this.searchobj.searchparams);
      }
    }
  }

 /*
  * Method to fetch cached contacts
  */
  getCachedContacts() {
    return this.cache.contacts;
  }

  /*
  * Method to check if contacts cached
  */
  isCached() {
    if (this.cache.contacts) {
      return true;
    }
    // tslint:disable-next-line:one-line
    else {
      return false;
    }
  }

  /*
  * Method to search contacts based on the flex value
  */
  search(flexValue, start, limit) {
    const params = {
      advancedSearch: true,
      start,
      limit,
    };

    if (Number(flexValue)) {
      if (flexValue.toString.length === ApplicationConstants.SSN_DIGIT) {
        // tslint:disable-next-line:no-string-literal
        params["ssn"] = flexValue;
      }

      if (flexValue.toString.length === ApplicationConstants.PHONENUMBER_DIGIT) {
        // tslint:disable-next-line:no-string-literal
        params["phone"] = flexValue;
      }
    }
    // tslint:disable-next-line:one-line
    else {
      // tslint:disable-next-line:no-string-literal
      params["name"] = flexValue;
    }
    return this.httpservice.get(Config.CONTACT_SEARCH_URL, params).map((response) => {
      return response.data;
    });
  }

  /*
  * Method to retrive the recent contacts
  */
  getRecentContacts(start, limit) {
    const params = {
      start,
      limit,
    };
    return this.httpservice.get(Config.RECENT_CONTACT_URL, params).map((response) => {
      return response.data;
    });
  }

  getTotal() {
    return this.httpservice.get(Config.CONTACT_LIST_URL)
      .map((response) => {
        this.cache.total = response.total;
        return response.total;
      });
  }

  /*
  * Filter borrower contacts
  */
  filterBorrowerContacts(contacts) {
    const tempContacts = contacts.filter((contact) => {
      return (contact.contactTypeId === "INDIVIDUAL");
    });
    return tempContacts;
  }

  /*
  * Filter borrower contacts
  */
  filterBusinessContacts(contacts) {
    const tempContacts = contacts.filter((contact) => {
      return (contact.contactTypeId !== "INDIVIDUAL");
    });
    return tempContacts;
  }
  getLoanCounts(contactId) {
    const params = {
      contactId,
    };
    return this.httpservice.post(Config.CONTACT_LOANCOUNTS_URL, params).map((response) => {
      return response;
    });
  }
  getContactDetails(contactId) {
    const params = {
      id: contactId,
    };

    return this.httpservice.get(Config.CONTACT_DETAILS_URL, params).map((response) => {
      this.cache.total = response.total;
      return response;
    });
  }
  getSalutationsList() {
    const salutationRequest = {page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.SALUTATION_URL, salutationRequest).map((response) => {
      return response;
    });
  }

  getTelephoneTypes() {
    const telephoneTypesRequest = {page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.TELEPHONETYPES_URL, telephoneTypesRequest)
    .map((response) => {
      return response;
    });
  }

  getContactTypes() {
    const contactTypesRequest = {page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.CONTACTTYPES_URL, contactTypesRequest).map((response) => {
      return response;
    });
  }

  getStates() {
    const contactTypesRequest = {page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.STATELOOKUP_URL, contactTypesRequest).map((response) => {
      return response;
    });
  }

  getCityStateLookup(zipcode) {
    const cityStateLookupRequest = {zipCode: zipcode, page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.CITYSTATELOOKUP_URL, cityStateLookupRequest)
    .map((response) => {
      return response;
    });
  }

  createContact(createContactRequest) {
    return this.httpservice.post(Config.CREATECONTACT_URL, createContactRequest).map((response) => {
      return response;
    });
  }

  updateContact(updateContactRequest) {
    return this.httpservice.post(Config.UPDATECONTACT_URL, updateContactRequest).map((response) => {
      return response;
    });
  }
  getCommunicationActivities(contactId) {
    const contactRequest = {contactId};
    return this.httpservice.get(Config.COMMUNICATION_ACTIVITIES_URL, contactRequest)
    .map((response) => {
      return response;
    });
  }
  getContactActivities(contactId) {
    const contactRequest = {contactId};
    return this.httpservice.get(Config.CONTACT_ACTIVITIES_URL, contactRequest).map((response) => {
      return response;
    });
  }
  addContactActivity(activityRequest) {
    return this.httpservice.post(Config.ADD_CONTACT_ACTIVITY_URL, activityRequest)
    .map((response) => {
      return response;
    });
  }
  updateRecentContact(contactId) {
    const contactRequest = {id: contactId};
    return this.httpservice.get(Config.UPDATE_RECENT_CONTACT_URL, contactRequest);
  }
  getContactsList(start, limit) {
    const params = {
      start,
      limit,
    };
    return this.httpservice.get(Config.CONTACT_LIST_URL, params).map((response) => {
      return response.data;
    });
  }
  getContacts(apiURL, params) {
    if (!params) {
      params = { start: 0, limit: 1000 };
    }
    // tslint:disable-next-line:one-line
    else {
      // tslint:disable-next-line:no-magic-numbers
      params.limit = 1000;
    }
    return this.httpservice.get(apiURL, params).map((response) => {
      return response.data;
    });
  }
}



