import { NavController, NavOptions } from "ionic-angular";
import { FeedbackService } from "../feedback/feedback.service";
import { Injectable } from "@angular/core";

@Injectable()
export class CustomNavService {

    constructor(public feedback: FeedbackService) {

    }

    push(navCtrl: NavController, page: any, params?: any, opts?: NavOptions, done?: () => void):
     Promise<any> {
        const loader = this.feedback.getLoader();
        loader.present();
        return navCtrl.push(page, params, opts, done).then((response) => {
            loader.dismiss();
            return response;
        });
    }
}



