import { Injectable } from "@angular/core";
import "rxjs/add/operator/map";

import { HttpService } from "../common/utils/http.service";
import { Config } from "../config/service-url.config";
import { Observable } from "rxjs/Observable";

const {
  CONTACT_DETAILS_URL,
  CREATE_EVENT_URL,
  DELETE_EVENT_URL,
  GET_EVENT_TYPES,
  LIST_EVENTS_URL,
  UPDATE_EVENT_URL,
} = Config;

@Injectable()
export class UpcomingEventsService {
  constructor(public http: HttpService) {}

  /**
   * Returns event types
   * @return { Observable }
   * {
   *  "total": 5,
   *   "data": [
   *      {
   *         "id": 1,
   *         "dateTypeId": "BIRTHDAY",
   *         "dateTypeDesc": "Birthday"
   *      }...
   *  }
   */
  public getUpcomingEvents() {
    const params = { page: 1, start: 0, limit: 25, contactId: 3245, dueBy: "END_OF_MONTH" };
    return this.http.get(LIST_EVENTS_URL, params);
  }

  public getUpcomingEventsForToday() {
    const params = { page: 1, start: 0, limit: 25, contactId: 3245, dueBy: "END_OF_DAY" };
    return this.http.get(LIST_EVENTS_URL, params).catch((response) => Observable.of({}));
  }

  public getContactDetails(contactId) {
    const params = { id: 3245 };
    return this.http.get(CONTACT_DETAILS_URL, params);
  }

  public addEvent(eventDetails) {
    return this.http.post(CREATE_EVENT_URL, eventDetails);
  }

  public updateEvent(eventDetails) {
    return this.http.post(UPDATE_EVENT_URL, eventDetails);
  }

  public deleteEvent(eventId) {
    const accToken = this.http.getAccessToken();
    const url = `${DELETE_EVENT_URL}?access_token=${accToken}&id=${eventId}`;
    return fetch(url, {method: "post"})
      .then((res) => res.json());
    // return this.http.post(DELETE_EVENT_URL, {id: eventId});
  }
}



