import {
  Injectable,
} from "@angular/core";
import "rxjs/add/operator/map";

import {
  HttpService,
} from "../common/utils/http.service";
import {
  Config,
} from "../config/service-url.config";

@Injectable()
export class NotesService {

  constructor(public http: HttpService) {}

  public getNotes(params, type) {
    const url = type === "contact" ? Config.NOTES_LIST_URL : type ===
      "contact_task" ? Config.NOTES_CONTACT_TASK_LIST_URL : type ===
      "loan_task" ? Config.NOTES_LOAN_TASK_LIST_URL : type ===
      "quote" ? Config.QUOTE_GET_NOTES : Config.NOTES_COND_LIST_URL;
    return this.http.get(url, params);
  }

  public saveNote(params, type) {
    const url = type === "contact" ? Config.NOTES_SAVE_URL : type ===
      "contact_task" ? Config.NOTES_CONTACT_TASK_SAVE_URL : type ===
      "loan_task" ? Config.NOTES_LOAN_TASK_SAVE_URL : type ===
      "quote" ? Config.QUOTE_SAVE_NOTES : Config.NOTES_COND_SAVE_URL;
    return this.http.post(url, params);
  }

}



