import { Injectable } from "@angular/core";
import "rxjs/add/operator/map";

import { HttpService } from "../common/utils/http.service";
import { Config } from "../config/service-url.config";

/*
  Generated class for the HomeService provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular 2 DI.
*/
@Injectable()
export class HomeService {

  constructor(public http: HttpService) {}

  public getDashboardCounts() {
    return this.http.get(Config.COUNT_LIST);
  }

  public getLoanCounts() {
    return this.http.get(Config.LOAN_LIST_URL, { start: 0, limit: 1 });
  }

  public getPipelineCounts() {
    return this.http.get(Config.PIPLINE_SUMMARY_URL);
  }

  public getReferralCounts() {
    return this.http.get(Config.LIST_REFERRAL_ACCOUNTS);
  }

  public getAppointmentsCounts(startDate, endDate) {
    const params = {
      startDate,
      endDate,
    };
    return this.http.get(Config.LIST_APPOINTMENTS_URL, params);
  }
}



