import { Component, ViewChild, NgZone, HostListener } from "@angular/core";
import { SplashScreen} from "@ionic-native/splash-screen";
import { StatusBar } from "@ionic-native/status-bar";
import { Platform, NavController, App, MenuController,  ModalController, Content} from "ionic-angular";
import { Subscription } from "rxjs/Subscription";
import { AuthService } from "../providers/auth.service";
import { HomePage } from "../pages/mobile/home/home";
import { HomeService } from "../providers/home.service";
import { MenuService } from "../providers/menu.service";
import { HttpService } from "../common/utils/http.service";
import { CustomNavService } from "../providers/nav/custom-nav.service";
import { LoanService } from "../providers/loans/loan.service";
import { FeedbackMessage } from "../providers/feedback/feedback.messages";
import { FeedbackService } from "../providers/feedback/feedback.service";
import { Config } from "../config/service-url.config";

@Component({
  templateUrl: "app.html",
})
export class MyApp {
  @ViewChild("appContent") nav: NavController;
  @ViewChild(Content) content: Content;
  rootPage: any = "LoginPage";
  username: string;
  contactsCount: number;
  tasksCount: number;
  loanCount: number;
  referralCount: number;
  scheduleCount: number;
  eventsCount: number;
  quotesCount: number;
  firstTime: boolean = true;
  firstLoad: boolean = true;
  pages: Array<{ id: string, title: string, pageName: string, count: string, icon: string, iconBgColor: string }>;
  activePage: any = "Dashboard";
  displayMenu: boolean = true;
  hasSessionExpired: Subscription;
  private pictureURL: string = "assets/images/steve.jpg";
  private showSplitPane: boolean = false;
  constructor(
    private platform: Platform,
    statusBar: StatusBar,
    splashScreen: SplashScreen,
    private auth: AuthService,
    private homeService: HomeService,
    private ngZone: NgZone,
    private app: App,
    private http: HttpService,
    private navService: CustomNavService,
    private menuService: MenuService,
    private menuCtrl: MenuController,
    public modalCtrl: ModalController,
    private loanService: LoanService,
    private feedbackService: FeedbackService,
  ) {
    platform.ready().then(() => {
      statusBar.styleDefault();
      splashScreen.hide();
    });
    this.initPages();
    this.onResize();
  }

  ngOnInit() {
    // this.persistPage();
    // Logout if the session has expired
    this.hasSessionExpired = this.http.hasSessionExpiredObservable.subscribe((value) => {
      if (value) {
        this.logout();
      }
    });
  }

  /**
   * 1) Observe changes everytime a new page is entered
   * 2) If it is a fresh instance of the app and if a entry for the last visted
   *  page exists in the local storage, navigate to that page to resume state
   * 3) Set the new instance flag to false and set the new page entered into the local storage
   */
  persistPage() {
    this.nav.viewDidEnter.subscribe((view) => {
      const pageName = view.instance.constructor.name;
      if (typeof (Storage) !== "undefined") {
        if (this.firstLoad &&
          window.localStorage.getItem("currentLocation") &&
          window.localStorage.getItem("currentLocation") !== pageName) {
          this.firstLoad = false;
          this.nav.setRoot(window.localStorage.getItem("currentLocation")).then(
            () => {
              // tslint:disable:no-magic-numbers
              this.activePage = window.localStorage.getItem("currentLocation");
              if (window.innerWidth >= 800 && window.localStorage.getItem("currentLocation") !== "Dashboard"
                && window.localStorage.getItem("currentLocation") !== "LoginPage") {
                this.showSplitPane = true;
              }
            },
          );
        }
        this.firstLoad = false;
        window.localStorage.setItem("currentLocation", pageName);
      }
    });
  }

  menuOpened() {
    if (this.firstTime) {
      this.getLiveCounts();
      this.username = this.auth.getUserName();
      this.firstTime = false;
    }
  }

  @HostListener("window:resize")
  onResize() {
    // tslint:disable-next-line:no-magic-numbers
    if (window.innerWidth >= 800 && this.activePage !== "Dashboard") {
      this.showSplitPane = true;
    }
    else {
      this.showSplitPane = false;
    }
  }

  openMenuContentPage(menuItem: string) {
    if (this.activePage === "MyLoans") {
      this.loanService.unlockLockedLoans().subscribe((response) => {
        if (response && !response.success) {
          this.feedbackService.showError(FeedbackMessage.GENERIC_ERROR_MESSAGE.toString());
        }
      });
    }
    this.activePage = menuItem;
    this.onResize();
    this.nav.setRoot(menuItem);
  }

  getLiveCounts() {
    this.homeService.getDashboardCounts().subscribe(
      (response) => {
        this.contactsCount = response.contacts;
        this.tasksCount = response.tasks;
        this.referralCount = response.referalPartners;
        this.scheduleCount = response.appointments;
        this.eventsCount = response.events;
        this.quotesCount = response.quotes;
        this.ngZone.run(() => {
          // console.log('Value Updated !');
        });
      },
      (error) => {
        // console.log(error);
      },
    );
    this.homeService.getLoanCounts().subscribe(
      (response) => {
        this.loanCount = response.total;
        this.ngZone.run(() => {
          // console.log('Value Updated !');
        });
      },
      (error) => {
        // console.log(error);
      },
    );
  }

  logout() {
    this.app.getRootNav().setRoot("LoginPage").then(() => {
      this.auth.logout().subscribe((response) => {
        if (response && response.success) {
          this.menuCtrl.enable(false, "menu");
          this.auth.destroy();
          this.firstTime = true;
          this.firstLoad = true;
          this.showSplitPane = false;
          this.activePage = "Dashboard";
        }
        else {
          this.feedbackService.showError(FeedbackMessage.GENERIC_ERROR_MESSAGE.toString());
        }
      });
    });
  }

  authenticated() {
    if (this.auth.authentication) {
      this.menuOpened();
    }
    return this.auth.authentication;
  }

  isLargeScreen() {
    if ((this.platform.is("core") || this.platform.is("ipad") || this.platform.is("tablet"))
      && (this.platform.isLandscape())) {
      return true;
    }
    return false;
  }

  getValue(key) {
    return this[key];
  }

  checkActive(page) {
    return page === this.activePage;
  }

  initPages() {
    this.pages = [
      { id: "home", title: "Home", pageName: "Dashboard", count: "", icon: "icon-home", iconBgColor: "#396" },
      {
        id: "contacts", title: "My Contacts", pageName: "MyContacts",
        count: "contactsCount", icon: "icon-users", iconBgColor: "#4798ba",
      },
      {
        id: "partners", title: "My Referral Partners", pageName: "MyReferralPartners",
        count: "referralCount", icon: "icon-business1", iconBgColor: "#4772ba",
      },
      {
        id: "loans", title: "My Loans", pageName: "MyLoans",
        count: "loanCount", icon: "icon-house", iconBgColor: "#8d68ad",
      },
      {
        id: "quotes", title: "My Quotes", pageName: "MyQuotes",
        count: "quotesCount", icon: "icon-quotes", iconBgColor: "#ad68a9",
      },
      {
        id: "scorecard", title: "My Scorecard", pageName: "ScoreCardPage",
        count: "", icon: "icon-stats", iconBgColor: "#b55d7e",
      },
      {
        id: "tasks", title: "My Tasks", pageName: "MyTasks",
        count: "tasksCount", icon: "icon-task", iconBgColor: "#b5a35d",
      },
      {
        id: "schedule", title: "My Schedule", pageName: "MySchedule",
        count: "scheduleCount", icon: "icon-schedule-1", iconBgColor: "#9db55d",
      },
      {
        id: "events", title: "My Upcoming Events", pageName: "UpcomingEvents",
        count: "eventsCount", icon: "icon-events", iconBgColor: "#7ab55d",
      },
      {
        id: "tools", title: "My Tools", pageName: "MyTools", count: "",
        icon: "icon-calculator", iconBgColor: "#67bfba ",
      },
    ];

    this.activePage = this.pages[0].pageName;
  }

  openPictureEditor(pictureURL: string) {
    const pictureEditorModalWindow = this.modalCtrl.create("PictureEditor", {
      paramObject: {
        pictureURL,
      },
    }, {
        showBackdrop: true,
      });
    pictureEditorModalWindow.onDidDismiss((response) => {
      this.pictureURL = response.pictureURL;
    });
    pictureEditorModalWindow.present();
  }
}



