import {
  Injectable,
} from "@angular/core";
import {
  Platform,
} from "ionic-angular";

@Injectable()
export class MenuService {
  private menuToggle: boolean = true;

  constructor(public platform: Platform) {
    this.menuToggle = (this.platform.is("core") || this.platform.is("tablet")) ? true : false;
  }

  public shouldShow() {
    return this.menuToggle;
  }

  public toggleShow() {
    this.menuToggle = this.menuToggle ? false : true;
    this.shouldShow();
  }

  public resetToggle() {
    this.menuToggle = true;
  }

  public showHideMenu(show) {
    this.menuToggle = show;
    this.shouldShow();
  }

}



