import {
  Injectable,
} from "@angular/core";
import "rxjs/add/operator/map";
import {
  Headers,
} from "@angular/http";
import {
  HttpService,
} from "../../common/utils/http.service";
import {
  Config,
} from "../../config/service-url.config";
import {
  Observable,
} from "rxjs/Observable";
import {
  FeedbackService,
} from "../../providers/feedback/feedback.service";

@Injectable()
export class QuoteService {

  constructor(public http: HttpService,
              public feedback: FeedbackService) {}

  public getLeadType() {
    return this.http.get(Config.LEAD_TYPES)
      .map((response) => {
        return response.data.map((element) => ({
          value: element.leadTypeId,
          name: element.leadTypeDesc,
        }));
      });
  }

  public getLeadSource() {
    return this.http.get(Config.LEAD_SOURCES)
      .map((response) => {
        return response.data.map((element) => ({
          value: element.leadSourceId,
          name: element.leadSourceDesc,
        }));
      });
  }

  public getStatus() {
    return this.http.get(Config.QUOTE_STATUS)
      .map((response) => {
        return response.data.map((element) => ({
          value: element.quoteStatusTypeId,
          name: element.quoteStatusDescription,
        }));
      });
  }

  public updateStatus(quote, item) {
    const params = {
      quoteId: quote.quoteId,
      newStatus: item.value,
      newStatusDesc: item.name,
      oldStatusDesc: quote.quoteStatusDescription,
    };
    return this.http.post(Config.UPDATE_QUOTES_STATUS, params);
  }

  public getPropertyTypes() {
    return this.http.get(Config.BUILDING_TYPES);
  }

  public getProjectTypes() {
    return this.http.get(Config.PROJ_CLASS_TYPES);
  }

  public getTaxRates() {
    return this.http.get(Config.TAX_RATE);
  }

  public getMIPlanTypes() {
    return this.http.get(Config.PMI_PLAN_TYPES);
  }

  public getCreditRatings() {
    return this.http.get(Config.CREDIT_RATINGS);
  }

  public getDesiredPricingOptions() {
    return this.http.get(Config.RATE_LOCK_TYPE);
  }

  public getRefinancePurposeTypes() {
    return this.http.get(Config.REFI_PURPOSE_TYPES);
  }

  public getCounty(zipCode) {
    return this.http.get(Config.COUNTIES, {
      zipCode,
    });
  }

  public createQuote(params) {
    return this.http.post(Config.CREATE_QUOTE, params);
  }

  public readQuote(quoteId) {
    return this.http.get(Config.READ_QUOTE, {
      quoteId,
    });
  }

  public updateQuoteHome(params) {
    return this.http.post(Config.UPDATE_QUOTE_HOME, params);
  }

  public updatePurposeAndProperty(params) {
    return this.http.post(Config.QUOTES_UPDATE_PURPOSE_PROPERTY, params);
  }

  public updatePurchaseLoanScenario(params) {
    return this.http.post(Config.UPDATE_QUOTE_PURCHASE_LOAN_SCENARIO,
      params);
  }

  public updateRefinanceLoanScenario(params) {
    return this.http.post(Config.UPDATE_QUOTE_REFINANCE_LOAN_SCENARIO,
      params);
  }

  public updateEquityLoanScenario(params) {
    return this.http.post(Config.UPDATE_QUOTE_EQUITY_LOAN_SCENARIO,
      params);
  }

  public updatePricingGoals(params) {
    return this.http.post(Config.UPDATE_QUOTE_PRICING_GOALS, params);
  }

  public updateExistingLiens(params) {
    return this.http.post(Config.UPDATE_EXISTING_LIENS, params);
  }

  public getQuoteResults(params) {
    return this.http.post(Config.GET_QUOTE_RESULTS, params);
  }

  public getLTVRatios(params) {
    return this.http.post(Config.CALC_LTV_RATIO, params);
  }

  public getMyQuotes() {
    const params: any = {
      start: 0,
      limit: 25,
    };
    return this.http.get(Config.QUOTES, params);
  }

  public getAvailableQuoteStatuses(quoteId, statusType) {
    const params: any = {
      quoteId,
      statusType,
      start: 0,
      page: 1,
      limit: 25,
    };
    return this.http.get(Config.AVAILABLE_QUOTE_STATUSES, params);
  }

  public getQuoteStatusHistory(quoteId) {
    return this.http.get(Config.READ_QUOTE_STATUS, {
      quoteId,
    });
  }

  public updateQuoteStatus(updateQuoteStatusRequest) {
    return this.http.post(Config.UPDATE_QUOTES_STATUS,
      updateQuoteStatusRequest);
  }

  public getMortgageTypes() {
    return this.http.get(Config.MORTGAGE_TYPES);
  }

  public getLiablilityStatusTypes() {
    return this.http.get(Config.LIABILITY_STATUS_TYPES);
  }

  public saveDesiredProducts(params) {
    return this.http.post(Config.UPDATE_QUOTE_DESIRED_PRODUCTS, params);
  }

  public emailPDF(params) {
    const formData = new FormData();
    Object.keys(params).forEach((key) => {
      formData.append(key, params[key]);
    });
    return this.http.post(Config.EMAIL_PDF, formData);
  }

  public downloadPDF(params) {
    const formData = new FormData();
    Object.keys(params).forEach((key) => {
      formData.append(key, params[key]);
    });
    return this.http.postFile(Config.DOWNLOAD_PDF, formData);
  }

  public getPaymentAndBalance(params) {
    return this.http.post(Config.PAYMENT_BALANCE, params);
  }

  public getQuoteFees(params) {
    return this.http.post(Config.QUOTE_FEES, params);
  }

  public getQuoteRates(params) {
    return this.http.post(Config.QUOTE_RATES, params);
  }

  public getQuoteStats() {
    const params: any = {
      intervalType: "ALL",
    };
    return this.http.post(Config.QUOTES_STATS, params);
  }
}



