// tslint:disable max-line-length
import { Injectable } from "@angular/core";
import "rxjs/add/operator/map";
import Moment from "moment";
import {
  CONTENT_TYPE_FORM,
  HttpService,
} from "../../common/utils/http.service";
import { Config } from "../../config/service-url.config";
import { Observable } from "rxjs/Observable";

@Injectable()
export class ScheduleService {

  constructor(public http: HttpService,
  ) {}

  public getAppointments(startDate, endDate): Observable<any> {
    const params = {
      startDate,
      endDate,
    };
    return this.http.get(Config.LIST_APPOINTMENTS_URL, params);
  }

  public createScheduleDetails(schedule): object {
    if (!schedule) {
      return {};
    }
    return {
      comment: schedule.hasOwnProperty("comment") ? schedule.comment : "",
      contacts: schedule.contacts,
      referralPartners: schedule.referralPartners,
      description: schedule.description,
      reminder: schedule.hasOwnProperty("reminder") ? schedule.reminder : "",
      startDate: schedule.hasOwnProperty("startDateTime") ? Moment(schedule.startDateTime).format("dddd Do MMMM YYYY") : "No Start DateTime", // "Saturday 24th June 2017" // tslint:disable max-line-length
      startTime: schedule.hasOwnProperty("startDateTime") ? Moment(schedule.startDateTime).format("LT") : "No Start Time", // "2:56 PM"
      endTime: schedule.hasOwnProperty("endDateTime") ? Moment(schedule.endDateTime).format("LT") : "No End Time", // "2:56 PM"
    };
  }
  public deleteAppointment(scheduleId: any) {
    const params = {
      appointmentId: scheduleId,
    };
    return this.http.post(Config.DELETE_UPPOINTMENT_URL, params);
  }
  public addAppointment(appointmentRequest): Observable<any> {
    return this.http.post(Config.ADD_APPOINTMENT, appointmentRequest);
  }
  public editAppointment(appointmentRequest): Observable<any> {
    return this.http.post(Config.EDIT_APPOINTMENT, appointmentRequest);
  }
  public getUsersList() {
    const params = {
      page: 1,
      start: 0,
      limit: 25,
    };
    return this.http.get(Config.LIST_USERS, params);
  }

  public getAppointmentDetails(appointmentId) {
    const params = { appointmentId };
    return this.http.get(Config.READ_APPOINTMENT_DETAILS, params);
  }

  public getMyScheduleForToday() {
    const fromDate = Moment.utc().format();
    const toDate = Moment().format("YYYY-MM-DD") + "T23:59:59Z";
    return this.getAppointments(fromDate, toDate).catch((response) => Observable.of({}));
  }
}



