import { Injectable } from "@angular/core";
import "rxjs/add/operator/map";
import { HttpService } from "../common/utils/http.service";
import { Config } from "../config/service-url.config";
import { Filters } from "../common/utils/filters";

const SSN_LENGTH: number = 9;
const PHONE_LENGTH: number = 10;
const SSN = "ssn";
const PHONE = "phone";
const NAME = "name";

@Injectable()
export class TasksService {

  constructor(public http: HttpService, public filters: Filters) {}

  public getAllLoanTasks(params) {
    return this.http.get(Config.LIST_LOAN_TASKS_URL, params);
  }

  public changeStatus(task, status) {
    task.taskStatusId = status;
    if (task.category === "CONTACT_STANDARD") {
      return this.updateContactTask(task);
    }
    if (task.category === "HOMELOAN_STANDARD") {
      return this.updateLoanTask(task);
    }
  }

  public updateContactTask(contactTaskRequest) {
    return this.http.post(Config.UPDATE_CONTACT_TASK, contactTaskRequest);
  }

  public updateLoanTask(loanTaskRequest) {
    loanTaskRequest.noteText = loanTaskRequest.noteText ?
      loanTaskRequest.noteText : "";
    loanTaskRequest.taskAssignedToTeamId = loanTaskRequest.taskAssignedToTeamId ?
      loanTaskRequest.taskAssignedToTeamId : "";
    loanTaskRequest.taskAssignedToUserTypeId = loanTaskRequest.taskAssignedToUserTypeId ?
      loanTaskRequest.taskAssignedToUserTypeId : "";
    return this.http.post(Config.UPDATE_LOAN_TASK, loanTaskRequest);
  }

  public getContactTaskStatusHistory(contactTaskStatusHistoryRequest) {
    return this.http.get(Config.LIST_CONTACT_TASK_STATUS_HISTORY,
      contactTaskStatusHistoryRequest);
  }

  public getLoanTaskStatusHistory(loanTaskStatusHistoryRequest) {
    return this.http.get(Config.LIST_LOAN_TASK_STATUS_HISTORY,
      loanTaskStatusHistoryRequest);
  }

  public getTaskStatusList() {
    const params = {
      page: 1,
      start: 0,
      limit: 25,
    };
    return this.http.get(Config.LIST_TASK_STATUS, params);
  }

  public getUsersList() {
    const params = {
      page: 1,
      start: 0,
      limit: 25,
    };
    return this.http.get(Config.LIST_USERS, params);
  }

  public searchContacts(searchString) {
    const searchobj: any = {
      searchparams: {},
      searchtotal: "",
      currentpage: "",
    };
    const params: any = {
      advSearchYn: true,
      page: 1,
      start: 0,
      limit: 25,
    };

    if (Number(searchString)) {
      if (searchString.length === SSN_LENGTH) {
        params[SSN] = Filters.formatSSN(searchString);
      }

      if (searchString.length === PHONE_LENGTH) {
        params[PHONE] = Filters.formatPhoneNumber(searchString);
      }
    } else {
      params[NAME] = searchString;
    }

    searchobj.searchparams = params;
    return this.http.get(Config.CONTACT_FLEX_FIND, searchobj.searchparams);
  }

  public searchReferrals(searchString) {
    const params: any = {
      searchType: "simple",
      page: 1,
      start: 0,
      limit: 25,
      flexValue: "",
    };

    if (Number(searchString)) {
      if (searchString.length === PHONE_LENGTH) {
        params.flexValue = Filters.formatPhoneNumber(searchString);
      }
    } else {
      params.flexValue = searchString;
    }

    return this.http.get(Config.LIST_REFERRAL_ACCOUNTS, params);
  }

  public searchLoans(query) {
    const searchParam = {
      flexValue: query,
      advSearchYn: false,
      page: 1,
      start: 0,
      limit: 25,
    };
    return this.http.get(Config.LOAN_SEARCH_URL, searchParam);
  }

  public getTaskItems() {
    return this.http.get(Config.TASK_ITEMS);
  }

  public getTaskCatagories() {
    return this.http.get(Config.TASK_CATEGORIES);
  }

  public saveTask(type, params) {
    const URL = (type === "Loan")
                  ? Config.ADD_TASK_LOAN
                  : (type === "Contact")
                    ? Config.ADD_TASK_CONTACT
                    : Config.ADD_TASK_REFERRAL;
    return this.http.post(URL, params);
  }

  public getTasksList(url, params) {
    return this.http.get(url, params);
  }

  public getAllTasks() {
    return this.http.get(Config.LIST_ALL_TASKS_URL);
  }

  public getTaskDetails(taskId) {
    const taskDetailsParam = {
      id: taskId,
    };
    return this.http.get(Config.TASK_DETAILS, taskDetailsParam);
  }

}



