import { Injectable } from "@angular/core";
import { Http } from "@angular/http";
import "rxjs/add/operator/map";
import { UpcomingEventsService } from "../upcoming-events.service";
import { ScheduleService } from "../schedule/schedule.service";
import { TasksService } from "../tasks.service";
import { LoanService } from "../loans/loan.service";
import { Config } from "../../config/service-url.config";
import { Observable } from "rxjs/Observable";
import { AlertModel } from "../../common/model/dashboard/alert.model";
import { Search } from "../../common/model/search";
import moment from "moment";

@Injectable()
export class DashboardService {

  public eventTypes = [
    { icon: "icon-birthday", dateTypeDesc: "Birthday" },
    { icon: "icon-birthday", dateTypeDesc: "Spouse Birthday" },
    { icon: "icon-anniversary", dateTypeDesc: "Wedding Anniversary" },
    { icon: "icon-loan-closure", dateTypeDesc: "Closing on New Home Loan" },
    { icon: "icon-loan-closure", dateTypeDesc: "Closing on Refi Loan" }];

  taskParams: any = {
    sort: '[{"property":"dueDateTime","direction":"ASC"}]',
  };
  constructor(public http: Http,
              public eventsService: UpcomingEventsService,
              public scheduleService: ScheduleService,
              public tasksService: TasksService,
              public loanService: LoanService) {
  }

  public getTaskDueToday() {
    this.taskParams.dueBy = "END_OF_DAY";
    return this.tasksService.getTasksList(Config.LIST_DUE_TASKS_URL, this.taskParams);
  }

  public getEventsAndAppointments() {
    const services: any[] = [];
    services.push(this.eventsService.getUpcomingEventsForToday());
    services.push(this.scheduleService.getMyScheduleForToday());
    return Observable.forkJoin(...services);
  }

  getEventsAndAppointmentsAlerts(eventsAndAppointments): AlertModel[] {
    const eventAppointmentCards = [];
    if (this.isEventsAndAppointmentsEmpty(eventsAndAppointments)) {
      //  eventAppointmentCards.push(
      //    this.getEmptyEventsAndAppointments("No Events or Appointments Today"));
      return eventAppointmentCards;
    }
    eventAppointmentCards.push(
      ...this.getEventAlerts(eventsAndAppointments[0]));
    eventAppointmentCards.push(
      ...this.getAppointmentAlerts(eventsAndAppointments[1]));
    return eventAppointmentCards;
  }

  getEmptyEventsAndAppointments(feedback): AlertModel {
    const appointmentEventCard: AlertModel = new AlertModel();
    appointmentEventCard.display = true;
    appointmentEventCard.paragraph = [feedback];
    return appointmentEventCard;
  }

  isEventsAndAppointmentsEmpty(eventsAndAppointments) {
    let isEventsEmpty = false;
    let isAppointmentsEmpty = false;
    if (!eventsAndAppointments) {
      return true;
    }
    if (!eventsAndAppointments[0] || !eventsAndAppointments[0].data
      || eventsAndAppointments[0].data.length === 0) {
      isEventsEmpty = true;
    }
    if (!eventsAndAppointments[1] || !eventsAndAppointments[1].data
      || eventsAndAppointments[1].data.length === 0) {
      isAppointmentsEmpty = true;
    }
    if (isEventsEmpty && isAppointmentsEmpty) {
      return true;
    }
    return false;
  }

  public getTodaysTaskOverdue() {
    this.taskParams.dueBy = "PAST_DUE";
    return this.tasksService.getTasksList(Config.LIST_DUE_TASKS_URL, this.taskParams);
  }

  public getTaskOverDueAlerts(tasks) {
    const overDueTasks: AlertModel = new AlertModel();
    overDueTasks.header = this.getOverDueCount(tasks);
    overDueTasks.paragraph = ["Tasks Overdue"];
    overDueTasks.icon = "icon-overdue";
    overDueTasks.id = "overdue";
    overDueTasks.name = "Overdue Tasks";
    overDueTasks.display = true;
    overDueTasks.drilldown.page = "MyTasks";
    overDueTasks.drilldown.params = {
      segment: "TasksAll",
      filterBy: "Past Due",
      dueBy: "PAST_DUE",
    };
    return overDueTasks;
  }

  getOverDueCount(tasks) {
    return tasks.contactTasks.length +
      tasks.contactTasks.length + tasks.referalPartnerTasks.length;
  }

  getLoanAlerts(loans) {
    const loanAlerts = [];

    const drilldown1 = {page: "MyLoans", params: {alertParams: {url: Config.UNLOCKED_LOANS_LIST}}};
    const drilldown2 = {page: "MyLoans", params: {alertParams: {url: Config.EXPIRED_LOANS_LIST}}};
    const drilldown3 = {page: "MyLoans", params: {alertParams: {url: Config.EXPIRING_LOANS_LIST}}};
    const drilldown4 = {page: "MyLoans", params: {alertParams: {url: Config.EXPIRING_LOANS_LIST}}};

    if (loans && loans[0].success) {
      const loanNotLocked = this.getAlertModel(loans[0].count,
        ["Loans Not locked"], "icon-unlock", "unlock-loans", "Loans Not locked", true, drilldown1);
      loanAlerts.push(loanNotLocked);
    }

    if (loans && loans[1].success) {
      const rateExpired = this.getAlertModel(loans[1].count,
        ["Rate Locks Expired"], "icon-expired-ratelock",
        "rate-lock", "Rate Locks Expired", true, drilldown2);
      loanAlerts.push(rateExpired);
    }

    if (loans && loans[2].success) {
      const locksDue = this.getAlertModel(loans[2].count,
        ["Locks Coming Due in 5 Days"], "icon-due", "unlock-due",
        "Locks Coming Due in 5 Days", true, drilldown3);
      loanAlerts.push(locksDue);
    }

    const exceedServiceLevel = this.getAlertModel(loans[2].count,
      ["Loans Exceeding Service Level Days"], "icon-exceed", "exceding-level",
      "Loans Exceeding Service Level Days", true, drilldown4);
    loanAlerts.push(exceedServiceLevel);
    return loanAlerts;
  }

  public getTaskAlerts(tasks) {
    const drilldown1 = { page: "MyTasks", params: { segment: "TasksContacts", filterBy: "Due Today",
      dueBy: "END_OF_DAY"} };
    const drilldown2 = { page: "MyTasks", params: {  segment: "TasksLoans",  filterBy: "Due Today",
      dueBy: "END_OF_DAY"} };
    const drilldown3 = { page: "MyTasks", params: { segment: "TasksReferral", filterBy: "Due Today",
      dueBy: "END_OF_DAY"} };

    const contactTasks = this.getAlertModel(tasks.contactTasks.length,
      ["Contact-related Tasks Due Today"], "icon-contact-task",
      "contact-task", "Contact Related Tasks", true, drilldown1);

    const loanTasks = this.getAlertModel(tasks.loanTasks.length,
      ["Loan-related Tasks Due Today"], "icon-loan-task",
      "ref-task", "Loan Related Tasks", true, drilldown2);

    const partnersTask = this.getAlertModel(tasks.referalPartnerTasks.length,
      ["Referral-related Tasks Due Today"], "icon-ref-task",
      "referral-task", "Referal Related Tasks", true, drilldown3);

    return [loanTasks, contactTasks, partnersTask];
  }

  public getAppointmentAlerts(appointments) {
    const appointmentCards = [];
    if (!appointments || !appointments.data) {
      return;
    }
    appointments.data.forEach((appointment) => {
      const appointmentCard: AlertModel = new AlertModel();
      appointmentCard.id = "meeting";
      appointmentCard.icon = "icon-meeting";
      appointmentCard.display = true;
      if (appointment.contacts[0]) {
        appointmentCard.header = appointment.contacts[0].contactName;
      }
      appointmentCard.paragraph =
        [appointment.description, `${this.getFormattedDateTime(appointment.startDateTime)} -
         ${this.getFormattedDateTime(appointment.endDateTime)}`];
      appointmentCard.name = "Appointment";
      appointmentCards.push(appointmentCard);
      appointmentCard.drilldown.page = "ViewEvent";
      appointmentCard.drilldown.params = {
        appointmentId: appointment.appointmentId,
      };
    });
    return appointmentCards;
  }

  public getEventAlerts(upcomingEvents) {
    const eventCards = [];
    if (!upcomingEvents || !upcomingEvents.data) {
      return;
    }
    upcomingEvents.data.forEach((event) => {
      const eventCard: AlertModel = new AlertModel();
      eventCard.id = "event";
      eventCard.icon = this.getEventIcon(event.dateTypeDesc);
      eventCard.display = true;
      eventCard.header = event.contactName;
      eventCard.paragraph = [event.dateTypeDesc + " Today"];
      eventCard.name = "event";
      eventCard.drilldown.page = "EventDetails";
      eventCard.drilldown.params = event;
      eventCards.push(eventCard);
    });
    return eventCards;
  }

  public getLoanAlertsCount() {
    const services: any[] = [];
    services.push(this.loanService.getUnlockedLoansCount());
    services.push(this.loanService.getExpiredLoansCount());
    services.push(this.loanService.getExpiringLoansCount());
    return Observable.forkJoin(...services);
  }

  private getAlertModel(count: any, desc: string[],
                        icon: string, id: string, name: string, display: boolean, drilldown: any) {
    const alertModel: AlertModel = new AlertModel();
    alertModel.header = count;
    alertModel.paragraph = desc;
    alertModel.icon = icon;
    alertModel.id = id;
    alertModel.name = name;
    alertModel.display = display;
    alertModel.drilldown = drilldown;
    return alertModel;
  }

  private getEventIcon(dateTypeDesc) {
    return this.getEventTypeByDesc(dateTypeDesc)[0].icon;
  }

  private getEventTypeByDesc(dateTypeDesc) {
    return this.eventTypes.filter((eventType) => {
      return eventType.dateTypeDesc === dateTypeDesc;
    });
  }

  private getFormattedDateTime(dateTime) {
    return moment(dateTime).local().format("hh:mm A");
  }
}



