import { Component, OnInit, Input, OnChanges } from "@angular/core";

@Component({
  selector: "avatar",
  templateUrl: "avatar.html",
})
export class Avatar implements OnChanges {
  @Input("imageUrl") imageUrl: string;
  @Input("name") name: string;
  @Input("size") size: string = "45px";
  // @Input('background') background = this.getRandomColor();
  @Input("displayType") displayType: string;
  @Input("letters") letters: number = 2;
  borderradius: string = "";
  // tslint:disable-next-line:no-empty
  constructor() {
  }

  getRandomColor(): string {
    const letters = "0123456789ABCDEF".split("");
    let color = "#";
    // tslint:disable-next-line:no-magic-numbers
    for (let i = 0; i < 6; i++) {
      // tslint:disable-next-line:no-magic-numbers
      color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
  }

  ngOnChanges() {
    if (this.name && this.name.trim().length) {
      const nameInitials = this.name.match(/\b(\w)/g);
      // const nameLetters = nameInitials.slice(0, 3).join('');
      this.name = nameInitials.slice(0, 3).join("").toUpperCase();
      if (this.letters > 0) {
        this.name = this.name.substr(0, this.letters);
      }
    }

    switch (this.displayType) {
      case "rounded":
        this.borderradius = "5%";
        break;
      case "circle":
        this.borderradius = "50%";
        break;
      default:
        this.borderradius = "50%";
    }
  }

}



