// tslint:disable
import { Injectable } from "@angular/core";
import Moment from "moment";
import "rxjs/add/operator/map";

@Injectable()
export class Filters {

  /**
   * Currency filter
   * 12345 => $12,345.00
   * @param {Number} value
   * @param {String} Currency ($)
   * @param {Number} decimals Decimal places
   */

  public static currency(value?, currency?, decimals?) {
    const digitsRE = /(\d{3})(?=\d)/g;
    value = parseFloat(value);
    if (!isFinite(value) || (!value && value !== 0)) {
      return "";
    }
    currency = currency != null ? currency : "$";
    const SOME = 2;
    decimals = decimals != null ? decimals : SOME;
    const STRINGIFIED = Math.abs(value).toFixed(decimals);
    const interger = decimals
        ? STRINGIFIED.slice(0, -1 - decimals)
        : STRINGIFIED;
    const i = interger.length % 3;
    const head = i > 0
        ? (interger.slice(0, i) + (interger.length > 3 ? "," : ""))
        : "";
    const floatValue = decimals
        ? STRINGIFIED.slice(-1 - decimals)
        : "";
    const sign = value < 0 ? "-" : "";
    return sign + currency + head +
        interger.slice(i).replace(digitsRE, "$1,") +
        floatValue;
  }

  /**
   Percent filter
    10 => 10.00%
     20.012 => 20.012%
     0 => 0.00%
     @param {Number} value
     @param {Number} decimals Decimal places
   */
  public static percent(value, decimals) {
   value = parseFloat(value);
   if (!isFinite(value) || (!value && value !== 0)) {
     return "";
   }
   decimals = decimals != null ? decimals : 2;
   if (Number.isInteger(value)) {
     const stringified = Math.abs(value).toFixed(decimals);
     return `${stringified}%`;
   }
   return `${value}%`;
  }
  //
  public static stripPhoneFormatting(phone) {
    return phone.replace(/[^\d]/g, "");
  }
  //
  public static formatPhoneNumber(phone) {
    phone = Filters.stripPhoneFormatting(phone);
    return "(" + phone.substr(0, 3) + ") " + phone.substr(3, 3) + "-" + phone.substr(6, 4);
  }

  public static formatSSN(ssn) {
    return ssn.substr(0, 3) + "-" + ssn.substr(3, 2) + "-" + ssn.substr(5, 4);
  }

  // public camelize(str) {
  //   return str
  //       .replace(/\s(.)/g, function($1) { return $1.toUpperCase() })
  //       .replace(/\s/g, "")
  //       .replace(/^(.)/, function($1) { return $1.toLowerCase() });
  // }
  //
  // tslint:disable-next-line:member-ordering
  public static concat(value1, value2, delimiter?): string {
    if (!value2 && !value1) {
      return "";
    }
    delimiter = delimiter != null ? delimiter : "/";
    if (!value1) {
      return `${delimiter} ${value2}`;
    }
    if (!value2) {
      return `${value1} ${delimiter}`;
    }
    return `${value1} ${delimiter} ${value2}`;
  }
  // public formatDateTime(dateTime) {
  //   if (!this.isDate(dateTime)) {
  //     return "";
  //   }
  //   return Moment(dateTime).format("MM/DD/YYYY HH:mm A");
  // }
  //
  // public calendar(date) {
  //   if (!this.isDate(date)) {
  //     return "";
  //   }
  //   date = date.toString();
  //   return Moment(new Date(date)).calendar(null, {
  //     sameDay: "[Today] HH:mm A",
  //     nextDay: "[Tomorrow] HH:mm A",
  //     nextWeek: "MM/DD/YYYY HH:mm A",
  //     lastDay: "[Yesterday] HH:mm A",
  //     lastWeek: "MM/DD/YYYY HH:mm A",
  //     sameElse: "MM/DD/YYYY HH:mm A",
  //   });
  // }
  // Returns USA FORMAT MM/DD/YYYY
  public static formatDate(date) {
    if (!Filters.isDate(date)) {
      return "";
    }
    return Moment(date).format("MM/DD/YYYY");
  }

  // format Currency - Billions like  $1B, Millions like $1M, Thousands like $1K
  public static formatCurrencyShort(num, doNotShowCurrency?: boolean, enableHtml = false) {
    let currency = doNotShowCurrency ? '' : '$';
    if (num >= 1000000000) {
      return currency + (num / 1000000000).toFixed(1).replace(/\.0$/, "") + (enableHtml ? "<i>B</i>" : "B");
    }
    if (num >= 1000000) {
      return currency + (num / 1000000).toFixed(1).replace(/\.0$/, "") + (enableHtml ? "<i>M</i>" : "M");
    }
    if (num >= 1000) {
      return currency + (num / 1000).toFixed(1).replace(/\.0$/, "") + (enableHtml ? "<i>K</i>" : "K");
    }
    return currency + num;
  }

  // /**
  //    get Date in the format "Saturday 24th June 2017"
  //    "Saturday 24th June 2017"
  //    @param {date} Date
  // */

  public static getFullDateString(date: Date): string {
    return Moment(date).format("dddd Do MMMM YYYY");
  }

  // /**
  //   * get Time in the format "2:56 PM"
  //   * "2:56 PM"
  //   * @param {date} Date
  // */

  public static getTimeString(date: Date): string {
    return Moment(date).format("LT");
  }

  // Method to check if the string can be parsed to date
  public static isDate(date: any): boolean {
    if (!date) {
      return false;
    }
    if (isNaN(Date.parse(date))) {
      return false;
    }
    return true;
  }

  public static removePercent(value: string) {
    return parseFloat(value.toString().replace("%", ""));
  }

  public static getFullMonth(monthShortForm) {
    const months = ["January", "February", "March", "April", "May", "June", 
    "July", "August", "September", "October", "November", "December", ""]; 
    var myDate = new Date(monthShortForm + " 1, 2000");
    var monthDigit = myDate.getMonth();
    return months[isNaN(monthDigit) ? 12 : (monthDigit)];
  }

  // returns in objects- { currency: '$', value: '2.5', denomination: 'B'}
  public static formatCurrencyShortObject(num, doNotShowCurrency?: boolean) {
    let currency = doNotShowCurrency ? '' : '$';
    if (num >= 1000000000) {
      return { currency, value: (num / 1000000000).toFixed(1).replace(/\.0$/, ""), denomination: "B" };
    }
    if (num >= 1000000) {
      return { currency, value: (num / 1000000).toFixed(1).replace(/\.0$/, ""), denomination: "M" };
    }
    if (num >= 1000) {
      return { currency, value: (num / 1000).toFixed(1).replace(/\.0$/, ""), denomination: "K" };
    }
    return { currency, value: num, denomination:''}
  }

}



