import { Injectable } from "@angular/core";
import "rxjs/add/operator/map";
import { HttpService } from "../../common/utils/http.service";
import { Config } from "../../config/service-url.config";
import { Observable } from "rxjs/Observable";
import { Filters } from "../../common/utils/filters";
import { LoanSummaryModel } from "../../common/model/loan/loan-summary.model";
import { LoanModel } from "../../common/model/loan/loan.model";
import { BorrowerModel } from "../../common/model/loan/borrower.model";
import { PropertyModel } from "../../common/model/loan/property.model";
import { FeedbackService } from "../../providers/feedback/feedback.service";

@Injectable()
export class LoanService {

  constructor(public http: HttpService,
              public feedback: FeedbackService) { }

  public getAllLoanStatus() {
    return this.http.get(Config.LOAN_STATUS_TYPES)
      .map((response) => {
        let result = [];
        if (response && response.data) {
          result = response.data.map((element) => ({
            value: element.loanStatusTypeId, name: element.loanStatusDescription,
          }),
          );
          result.push(...this.getStaticLoanTypes());
          return result;
        }
      });
  }

  public getAllLockStatus() {
    return this.http.get(Config.LOAN_LOCK_STATUS_TYPES)
      .map((response) => {
        return response.data.map((element) => ({
          value: element.rateLockStatusTypeId,
          name: element.rateLockStatusTypeDesc,
        }),
        );
      });
  }

  public getAllLoanPurpose() {
    return this.http.get(Config.LOAN_PURPOSE_TYPES)
      .map((response) => {
        return response.data.map((element) => ({
          value: element.loanPurposeTypeId, name: element.loanPurposeTypeDesc,
        }),
        );
      });
  }

  public getMyPipelines() {
    return this.http.get(Config.PIPLINE_SUMMARY_URL);
  }

  public getAuthLoanId(loanNumber: string): Observable<any> {
    const params = {
      lenderLoanNumber: loanNumber,
    };
    return this.http.get(Config.OPEN_LOAN_URL, params);
  }

  setBorrowerDetails(borrowers, borrowerLoanDetails) {
    if (!borrowerLoanDetails) {
      return;
    }
    borrowerLoanDetails.forEach((borrower) => {
      const borrowerInfo: BorrowerModel = new BorrowerModel();
      borrowerInfo.income = Filters.currency(borrower.borrowerIncome, "$", 0);
      borrowerInfo.assets = Filters.currency(
        borrower.borrowerTotalAssets, "$", 0,
      );
      borrowerInfo.liabilites = Filters.currency(
        borrower.borrowerTotalLiabilities, "$", 0,
      );
      borrowerInfo.telephone = borrower.borrowerContactNumber;
      borrowerInfo.name = borrower.borrowerName;
      borrowerInfo.type = borrower.borrowerRoleId;
      borrowerInfo.email = borrower.emailAddress;
      borrowerInfo.contactId = borrower.contactId;
      borrowers.push(borrowerInfo);
    });
  }

  setSummary(loan: LoanModel, readSummary) {
    if (!readSummary) {
      return;
    }
    loan.status = readSummary.loanStatusDesc;
    loan.intrest = Filters.percent(readSummary.interestRate, 2);
    loan.ltvcltv = Filters.concat(
      Filters.percent(readSummary.ltvRatioPct, 2),
      Filters.percent(readSummary.cltvRatioPct, 2),
    );
    loan.product = readSummary.loanProductDesc;
    loan.program = readSummary.loanProgramTypeDesc;
    loan.purpose = readSummary.loanPurposeTypeDesc;
    loan.creditScore = readSummary.qualCreditScore;
    loan.frontBackRatio = Filters.concat(
      Filters.percent(readSummary.housingExpenseRatio, 2),
      Filters.percent(readSummary.debtRatio, 2),
    );
    loan.amount = Filters.currency(readSummary.totalLoanAmt, "$", 2);
    loan.amountWithoutFormat = readSummary.totalLoanAmt;
  }

  setPropertyInfo(property, subjectProperty, summary) {
    if (!property || !subjectProperty || !summary){
      return;
    }
    property.address = `${subjectProperty.addressLineOne}, ${subjectProperty.city},
     ${subjectProperty.stateId} ${subjectProperty.postalCodeId}`;
    property.type = subjectProperty.propertyTypeId;
    property.occupancy = summary.occupancyTypeDesc;
    property.purchasePrice = Filters.currency(summary.purchasePrice, "$", 2);
  }

  getLoanSummary(loanNumber: string): Observable<LoanSummaryModel> {
    const params = {
      loanNumber,
    };
    const loader = this.feedback.getLoader();
    loader.present();
    return this.http.get(Config.LOAN_SUMMARY_AGGREGATOR_API, params)
      .map((response) => {
        const loanSummaryModel = new LoanSummaryModel();
        const loan = new LoanModel();
        const borrowers: BorrowerModel[] = [];
        const propertyInfo = new PropertyModel();
        this.setSummary(loan, response.loanSummary);
        loan.loanNumber = loanNumber;
        this.setBorrowerDetails(borrowers, response.borrowers);
        this.setPropertyInfo(propertyInfo, response.propertyInfo, response.loanSummary);
        loanSummaryModel.loan = loan;
        loanSummaryModel.borrowers = borrowers;
        loanSummaryModel.property = propertyInfo;
        return loanSummaryModel;
      }).finally(() => {
        loader.dismiss();
      });
  }

  getStaticLoanTypes() {
    return [
      {
        name: "0000..0099 - All Lead",
        value: "0000..0099",
      },
      {
        name: "0100..0199 - All Prospect",
        value: "0100..0199",
      },
      {
        name: "0200..0299 - All Application",
        value: "0200..0299",
      },
      {
        name: "0300..0399 - All Set-Up ",
        value: "0300..0399",
      },
      {
        name: "0400..0499 - All Processing",
        value: "0400..0499",
      },
      {
        name: "0500..0599 - All Underwriting",
        value: "0500..0599",
      },
      {
        name: "0600..0699 - All Approved",
        value: "0600..0699",
      },
      {
        name: "0700..0799 - All Clear to Close",
        value: "0700..0799",
      },
      {
        name: "0800..0899 - All Closing",
        value: "0800..0899",
      },
      {
        name: "0900..1099 - All Funding",
        value: "0900..1099",
      },
      {
        name: "1100..1199 - All Post Closing",
        value: "1100..1199",
      },
      {
        name: "1200..1299 - All Delivery",
        value: "1200..1299",
      },
      {
        name: "1300..1399 - All Withdrawn",
        value: "1300..1399",
      },
      {
        name: "1400..1499 - All Cancelled",
        value: "1400..1499",
      },
      {
        name: "1500..9999 - All Denied ",
        value: "1500..9999",
      },
    ];
  }

  getUnlockedLoansCount() {
    return this.http.get(Config.UNLOCKED_LOANS_COUNT);
  }

  getExpiredLoansCount() {
    return this.http.get(Config.EXPIRED_LOANS_COUNT);
  }

  getExpiringLoansCount() {
    return this.http.get(Config.EXPIRING_LOANS_COUNT);
  }

  getLoanLockedStatus(lenderLoanNumber) {
    const loanLockedStatusRequest = { lenderLoanNumber};
    let formData: FormData = new FormData();
    formData.append("lenderLoanNumber", lenderLoanNumber);
    return this.http.post(Config.GET_LOAN_LOCKED_STATUS, formData);
  }

  unlockLockedLoans() {
    return this.http.get(Config.UNLOCK_LOCKED_LOANS);
  }
}



