import {
  Injectable,
} from "@angular/core";
import "rxjs/add/operator/map";

import {
  HttpService,
} from "../common/utils/http.service";
import {
  Config,
} from "../config/service-url.config";

@Injectable()
export class AffordabilityCalculatorService {

  constructor(public http: HttpService) {}

  public getAffordabilityResult(params) {
    return this.http.get(Config.AFFORDABILITY_CALCULATOR, params);
  }
  public getRequiredIncomeResult(params) {
    return this.http.get(Config.REQUIRED_INCOME_CALCULATOR, params);
  }
  public getMaxLoanAmountResult(params) {
    return this.http.get(Config.MAX_LOAN_AMOUNT_CALCULATOR, params);
  }
}



