import { Injectable } from "@angular/core";
import { Http } from "@angular/http";
import "rxjs/add/operator/map";
import { HttpService } from "../common/utils/http.service";
import { Config } from "../config/service-url.config";

/*
  Generated class for the ReferralPartnerService provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular 2 DI.
*/
@Injectable()
export class ReferralPartnerService {

  constructor(public httpService: HttpService) {
  }

  getAccountsList(start, limit) {
    const params = {
      start,
      limit,
    };
    return this.httpService.get(Config.LIST_REFERRAL_ACCOUNTS, params).map((response) => {
      return response.data;
    });
  }

  getPartnersList(start, limit) {
    const params = {
      start,
      limit,
    };
    return this.httpService.get(Config.LIST_REFERRAL_CONTACTS, params).map((response) => {
      return response.data;
    });
  }

  getReferralPartners(apiURL, params) {
    if (!params) {
      params = { start: 0, limit: 1000 };
    }
    // tslint:disable-next-line:one-line
    else {
      // tslint:disable-next-line:no-magic-numbers
      params.limit = 1000;
    }
    return this.httpService.get(apiURL, params).map((response) => {
      return response.data;
    });
  }

  getContactDetails(contactId) {
    const params = {
      id: contactId,
    };

    return this.httpService.get(Config.REFERRAL_CONTACT_DETAILS, params)
    .map((response) => {
      return response;
    });
  }
  getAccountDetails(accountId) {
    const params = {
      id: accountId,
    };

    return this.httpService.get(Config.REFERRAL_ACCOUNT_DETAILS, params)
    .map((response) => {
      return response;
    });
  }
  getSalutationsList() {
    const salutationRequest = {page: 1, start: 0, limit: 25};
    return this.httpService.get(Config.SALUTATION_URL, salutationRequest)
    .map((response) => {
      return response;
    });
  }

  getTelephoneTypes() {
    const telephoneTypesRequest = {page: 1, start: 0, limit: 25};
    return this.httpService.get(Config.TELEPHONETYPES_URL, telephoneTypesRequest)
    .map((response) => {
      return response;
    });
  }

  getContactTypes() {
    const contactTypesRequest = {page: 1, start: 0, limit: 25};
    return this.httpService.get(Config.CONTACTTYPES_URL, contactTypesRequest)
    .map((response) => {
      return response;
    });
  }

  getStates() {
    const contactTypesRequest = {page: 1, start: 0, limit: 25};
    return this.httpService.get(Config.STATELOOKUP_URL, contactTypesRequest)
    .map((response) => {
      return response;
    });
  }

  getCityStateLookup(zipcode) {
    const cityStateLookupRequest = {zipCode: zipcode, page: 1, start: 0, limit: 25};
    return this.httpService.get(Config.CITYSTATELOOKUP_URL, cityStateLookupRequest)
    .map((response) => {
      return response;
    });
  }

  createContact(createContactRequest) {
    return this.httpService.post(Config.CREATECONTACT_URL, createContactRequest)
    .map((response) => {
      return response;
    });
  }

  updateContact(updateContactRequest) {
    return this.httpService.post(Config.UPDATECONTACT_URL, updateContactRequest)
    .map((response) => {
      return response;
    });
  }
  getCommunicationActivities(contactId) {
    const contactRequest = {contactId};
    return this.httpService.get(Config.COMMUNICATION_ACTIVITIES_URL,
    contactRequest).map((response) => {
      return response;
    });
  }
  getContactActivities(contactId) {
    const contactRequest = {contactId};
    return this.httpService.get(Config.CONTACT_ACTIVITIES_URL,
    contactRequest).map((response) => {
      return response;
    });
  }
  addContactActivity(activityRequest) {
    return this.httpService.post(Config.ADD_CONTACT_ACTIVITY_URL, activityRequest)
    .map((response) => {
      return response;
    });
  }
  updateRecentContact(contactId) {
    const contactRequest = {id: contactId};
    return this.httpService.get(Config.UPDATE_RECENT_CONTACT_URL,
    contactRequest).map((response) => {
      return response;
    });
  }
  getReferralAccountContacts(partnerId) {
    const referralAccountContactsRequest = {partnerId};
    return this.httpService.get(Config.LIST_REFERRAL_ACCOUNT_CONTACTS,
    referralAccountContactsRequest).map((response) => {
      return response;
    });
  }
  createReferralAccount(createReferralAccountRequest) {
    return this.httpService.post(Config.CREATE_REFERRAL_ACCOUNT,
    createReferralAccountRequest).map((response) => {
      return response;
    });
  }
  updateReferralAccount(updateReferralAccountRequest) {
    return this.httpService.post(Config.UPDATE_REFERRAL_ACCOUNT,
    updateReferralAccountRequest).map((response) => {
      return response;
    });
  }
  searchCompanyNames(searchCompanyNamesRequest) {
    return this.httpService.get(Config.SEARCH_COMPANY_NAMES,
    searchCompanyNamesRequest);
  }
  createReferralPartner(createReferralPartnerRequest) {
    return this.httpService.post(Config.CREATE_REFERRAL_PARTNER,
    createReferralPartnerRequest).map((response) => {
      return response;
    });
  }
  updateReferralPartner(updateReferralPartnerRequest) {
    return this.httpService.post(Config.UPDATE_REFERRAL_PARTNER,
    updateReferralPartnerRequest).map((response) => {
      return response;
    });
  }
  searchCompanyAssociate(searchCompanyAssociateRequest) {
    return this.httpService.get(Config.SEARCH_COMPANY_ASSOCIATE,
    searchCompanyAssociateRequest).map((response) => {
      return response;
    });
  }

}



