import { Injectable } from "@angular/core";
import { Http } from "@angular/http";
import "rxjs/add/operator/map";
import { HttpService } from "../common/utils/http.service";
import { Filters } from "../common/utils/filters";
import { Config } from "../config/service-url.config";
import { ApplicationConstants } from "../config/application-constants.config";
import { LoanService } from "./loans/loan.service";
import { Observable } from "rxjs/Observable";

@Injectable()
export class PricingService {
  constructor(public httpservice: HttpService,
              public loanService: LoanService) {
  }

  getCityStateLookup(zipcode) {
    const cityStateLookupRequest = { zipCode: zipcode, page: 1, start: 0, limit: 25 };
    return this.httpservice.get(Config.CITYSTATELOOKUP_URL, cityStateLookupRequest)
      .map((response) => {
        return response;
      });
  }

  getSubjectPropertyTypes(authLoanId) {
    const subjectPropertyTypesRequest = { authLoanId, page: 1, start: 0, limit: 25 };
    return this.httpservice.get(Config.GET_SUBJECT_PROPERTY_TYPES, subjectPropertyTypesRequest)
      .map((response) => {
        return response;
      });
  }

  getProjectTypes(authLoanId) {
    const subjectPropertyTypesRequest = { authLoanId, page: 1, start: 0, limit: 25 };
    return this.httpservice.get(Config.GET_PROJECT_TYPES, subjectPropertyTypesRequest)
      .map((response) => {
        return response;
      });
  }

  getPropertyTypes(authLoanId) {
    const subjectPropertyTypesRequest = { authLoanId, page: 1, start: 0, limit: 25 };
    return this.httpservice.get(Config.GET_PROPERTY_TYPES, subjectPropertyTypesRequest)
      .map((response) => {
        return response;
      });
  }

  updateProperty(updatePropertyRequest) {
    return this.httpservice.post(Config.UPDATE_PROPERTY_INFO, updatePropertyRequest)
      .map((response) => {
        return response;
      });
  }

  getLoanHeaderInfo(authLoanId, loanId) {
    const loanHeaderInfoRequest = { authLoanId, loanId };
    return this.httpservice.get(Config.GET_PROJECT_TYPES, loanHeaderInfoRequest)
      .map((response) => {
        return response;
      });
  }

  getLoanPurposes(authLoanId) {
    const loanPurposesRequest = { authLoanId, page: 1, start: 0, limit: 25 };
    return this.httpservice.get(Config.GET_LOAN_PURPOSES, loanPurposesRequest)
      .map((response) => {
        return response;
      });
  }

  getRefinancePurposes(authLoanId) {
    const refinancePurposesRequest = { authLoanId, page: 1, start: 0, limit: 25 };
    return this.httpservice.get(Config.GET_REFINANCE_PURPOSES, refinancePurposesRequest)
      .map((response) => {
        return response;
      });
  }

  getDocumentationTypes(authLoanId) {
    const documentationTypesRequest = { authLoanId, page: 1, start: 0, limit: 25 };
    return this.httpservice.get(Config.GET_DOCUMENTATION_TYPES, documentationTypesRequest)
      .map((response) => {
        return response;
      });
  }

  getOccupancyTypes(authLoanId) {
    const occupancyTypesRequest = { authLoanId, page: 1, start: 0, limit: 25 };
    return this.httpservice.get(Config.GET_OCCUPANCY_TYPES, occupancyTypesRequest)
      .map((response) => {
        return response;
      });
  }

  saveAllLoanTerms(loanTermsRequest) {
    return this.httpservice.post(Config.SAVE_ALL_LOAN_TERMS, loanTermsRequest)
      .map((response) => {
        return response;
      });
  }

  getLockPeriods(authLoanId) {
    const lockPeriodsRequest = { authLoanId, page: 1, start: 0, limit: 25 };
    return this.httpservice.get(Config.GET_LOCK_PERIODS, lockPeriodsRequest)
      .map((response) => {
        return response;
      });
  }

  getPricingAndRateLockInfo(authLoanId, id) {
    const pricingAndRateLockInfoRequest = {authLoanId, id};
    return this.httpservice.get(Config.GET_PRICING_RATELOCK_INFO,
      pricingAndRateLockInfoRequest)
      .map((response) => {
        return response;
      });
  }

  public getHomeLoanMappingDataWithLoanNumber(loanNumber: string): Observable<any> {
    return this.loanService.getAuthLoanId(loanNumber).flatMap((data) => {
      const pricingAndRateLockInfoRequest = {
        lenderLoanNumber: loanNumber,
        authLoanId: data.loanId,
        id: "",
      };
      return this.getHomeLoanMappingDataWithAuthId(pricingAndRateLockInfoRequest);
    });
  }

  getHomeLoanMappingDataWithAuthId(pricingAndRateLockInfoRequest) {
    return this.httpservice
      .get(Config.GET_HOME_LOAN_MAPPING_DATA,
      pricingAndRateLockInfoRequest)
      .map((response) => {
        return response;
      });
  }

  getRetailLoanTermsDataWithLoanNumber(loanNumber: string): Observable<any> {
    return this.loanService.getAuthLoanId(loanNumber).flatMap((data) => {
      const pricingAndRateLockInfoRequest = {
        lenderLoanNumber: loanNumber,
        authLoanId: data.loanId,
        id: "",
      };
      return this.getRetailLoanTermsDataWithAuthId(pricingAndRateLockInfoRequest);
    });
  }

  getRetailLoanTermsDataWithAuthId(pricingAndRateLockInfoRequest) {
    return this.httpservice
      .get(Config.GET_RETAIL_LOAN_TERMS
      , pricingAndRateLockInfoRequest)
      .map((response) => {
        return response;
      });
  }

  getPricingHistoryList(authLoanId, loanId) {
    let listPriceHistoryRequest = this.httpservice.convertJsonToQueryString({ authLoanId, loanId})
      .toString();
    listPriceHistoryRequest = "&" + listPriceHistoryRequest;
    return this.httpservice.post(Config.GET_PRICING_HISTORY, "", null, listPriceHistoryRequest)
      .map((response) => {
        return response;
      });
  }

  getRates(ratesRequest) {
    return this.httpservice.post(Config.GET_RATES, ratesRequest)
      .map((response) => {
        return response;
      });
  }

  getPricingAdjustments(pricingAdjustmentsRequest, authLoanId) {
    return this.httpservice.post(Config.GET_PRICING_ADJUSTMENTS, pricingAdjustmentsRequest)
    .map((response) => {
      return response;
    });
  }

  updatePricing(pricingRequest) {
    return this.httpservice.post(Config.UPDATE_PRICING, pricingRequest)
      .map((response) => {
        return response;
      });
  }

  getCoreChangeReasons(authLoanId) {
    const coreChangeReasonsRequest = { authLoanId, page: 1, start: 0, limit: 25 };
    return this.httpservice.get(Config.GET_CORE_CHANGE_REASONS, coreChangeReasonsRequest)
      .map((response) => {
        return response;
      });
  }

  getExceptionTypes(authLoanId) {
    const exceptionTypesRequest = { authLoanId, page: 1, start: 0, limit: 25 };
    return this.httpservice.get(Config.GET_EXCEPTION_TYPES, exceptionTypesRequest)
      .map((response) => {
        return response;
      });
  }

  getLoanTypes(authLoanId) {
    return this.httpservice.get(Config.GET_LOAN_TYPES, { authLoanId })
      .map((response) => {
        return response;
      });
  }

  getLienTypes(authLoanId) {
    return this.httpservice.get(Config.GET_LOAN_TYPES, { authLoanId })
      .map((response) => {
        return response;
      });
  }

  getLookUpData(lookupService: any[], authLoanId) {
    const lookup: any[] = lookupService.map((url) => {
      return this.httpservice.get(url, { authLoanId })
      .map((response) => {
        return response.data;
      }).catch((response) => Observable.of([]));
    });
    return Observable.forkJoin(...lookup);
  }

  getHomeLoanMappingData(homeLoanMappingdataRequest) {
    return this.httpservice.get(Config.GET_HOME_LOAN_MAPPING_DATA,
      homeLoanMappingdataRequest).map((response) => {
        return response;
      });
  }

  readTerms(authLoanId) {
    return this.httpservice.get(Config.READ_LOAN_TERMS,
      {id: authLoanId, authLoanId}).map((response) => {
        return response;
      });
  }

  getPropertyAddress(lenderLoanNumber, authLoanId) {
    return this.httpservice.get(Config.READ_SUBJECT_PROPERTY_URL,
    {lenderLoanNumber, authLoanId}).map((response) => {
        return response;
    });
  }

  // getLookUpData(url: any, authLoanId) {
  //   return this.httpservice.get(url, { authLoanId })
  //     .map((response) => {
  //       return response.data;
  //     });
  // }

  getLenderInfos(authLoanId) {
    const params = {authLoanId, page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.GET_LENDER_INFOS, params);
  }

  getFulfillmentCenters(authLoanId) {
    const params = {authLoanId, page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.GET_FULFILLMENT_CENTERS, params);
  }

  getSalesRegions(authLoanId) {
    const params = {authLoanId, page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.GET_SALES_REGIONS, params);
  }

  getSalesBranches(authLoanId) {
    const params = {authLoanId, page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.GET_SALES_BRANCHES, params);
  }

  getMarketTypes(authLoanId) {
    const params = {authLoanId, page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.GET_MARKET_TYPES, params);
  }

  getLeadTypes(authLoanId) {
    const params = {authLoanId, page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.GET_LEAD_TYPES, params);
  }

  getLeadSources(authLoanId) {
    const params = {authLoanId, page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.GET_LEAD_SOURCES, params);
  }

  getUserTeams(authLoanId) {
    const params = {authLoanId, page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.GET_USERTEAMS, params);
  }

  getBrokerInfos(authLoanId) {
    const params = {authLoanId, page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.GET_BROKERINFOS, params);
  }

  getSubChannels(authLoanId) {
    const params = {authLoanId, page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.GET_SUB_CHANNELS, params);
  }

  getSalesSubRegions(authLoanId) {
    const params = {authLoanId, page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.GET_SALESSUBREGIONS, params);
  }

  getProductPricingMappings(authLoanId) {
    const params = {authLoanId, page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.GET_PRODUCT_PRICING_MAPPINGS, params);
  }

  getBrokerTypes(authLoanId) {
    const params = {authLoanId, page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.GET_BROKER_TYPES, params);
  }

  getChannels(authLoanId) {
    const params = {authLoanId, page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.GET_CHANNELS, params);
  }

  getPromotions(authLoanId) {
    const params = {authLoanId, page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.GET_PROMOTIONS, params);
  }

  getAssignedTeams(authLoanId) {
    const params = {authLoanId, page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.GET_ASSIGNED_TEAMS, params);
  }

  getAssignedUsers(authLoanId) {
    const params = {authLoanId, page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.GET_ASSIGNED_USERS, params);
  }

  getLoanMappingData(authLoanId) {
    const params = {authLoanId, page: 1, start: 0, limit: 25};
    return this.httpservice.get(Config.GET_LOAN_MAPPING_DATA, params);
  }

  getLoanSourceDetails(authLoanId) {
    const params = {authLoanId, id: authLoanId};
    return this.httpservice.get(Config.GET_LOAN_SOURCE_DETAILS, params);
  }

}



