import { Injectable } from "@angular/core";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import { Observable } from "rxjs/Observable";

import { Session } from "../common/model/session";
import { User } from "../common/model/user";
import { HttpService } from "../common/utils/http.service";
import { Config } from "../config/service-url.config";
import { ExceptionService } from "../providers/feedback/exception.service";
import { FeedbackMessage } from "../providers/feedback/feedback.messages";
import { FeedbackService } from "../providers/feedback/feedback.service";
import { Headers } from "@angular/http";

@Injectable()
export class AuthService {
  currentUser: any;
  currentUserInfo: any = {};
  loginUrl: string = Config.LOGIN_URL;
  authentication: boolean = false;

  constructor(private http: HttpService,
              private session: Session,
              private exception: ExceptionService,
              private feedback: FeedbackService) {
  }

  public login(credentials) {
    if (credentials.username === null || credentials.password === null) {
      return this.exception
        .handleErrorResponseAsAlert("Login Error",
        FeedbackMessage.INSERT_CREDENTIALS);
    }
    return this.process(credentials);
  }

  public register(credentials) {
    if (credentials.username === null || credentials.password === null) {
      return Observable.throw("Please insert credentials");
    } else {
      // At this point store the credentials to your backend!
      return Observable.create((observer) => {
        observer.next(true);
        observer.complete();
      });
    }
  }

  public getUserInfo(): User {
    return this.currentUser;
  }

  public getUserName(): string {
    return this.currentUser.username;
  }

  public getUserId(): string {
    return this.currentUserInfo.userId;
  }

  public getUserFullName(): string {
    return this.currentUserInfo.fullName;
  }

  public destroy() {
    this.session.expire();
    this.session.set("currentUser", null);
    this.currentUser = null;
    this.authentication = false;
  }

  public logout() {
     return this.http.get(Config.LOGOUT_URL);
  }

  private process(credentials) {
    const loader = this.feedback.getLoader();
    loader.present();
    const queryString = this.http.convertJsonToQueryString({
       "userName-inputEl": credentials.username, "password-inputEl": credentials.password,
    }).toString();
    const headers = new Headers({ "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8" });
    return this.http.post(this.loginUrl, queryString, headers)
            .map((response) => {
                  if (response && response.success === true) {
                      this.session.set("currentUser", response);
                      this.currentUser = response;
                      this.authentication = true;
                      this.getAuthUser();
                      // this.setLocalStorageUser();
                  } else {
                      throw Error("Authentication Failed");
                  }
                  return this.authentication;
            })
            .catch((err) => {
              return this.exception
                      .handleErrorResponseAsAlert(err,
                           FeedbackMessage.LOGIN_ERROR);
            })
            .finally(() => {
              loader.dismiss();
            });
  }

  private setLocalStorageUser() {
    if (typeof (Storage) !== "undefined") {
      window.localStorage.setItem("currentUser", JSON.stringify(this.currentUser));
    }
  }

  private setLocalStorageUserInfo() {
    if (typeof (Storage) !== "undefined") {
      window.localStorage.setItem("currentUserInfo", JSON.stringify(this.currentUserInfo));
    }
  }

  private clearLocalStorage() {
    if (typeof (Storage) !== "undefined") {
      window.localStorage.clear();
    }
  }

  // tslint:disable-next-line:member-ordering
  getAuthUser() {
    return this.http.get(Config.USER_URL).subscribe((response) => {
      this.currentUserInfo = response;
      // this.setLocalStorageUserInfo();
    });
  }
}



