import { Injectable } from "@angular/core";
import "rxjs/add/operator/map";
import { HttpService } from "../../common/utils/http.service";
import { Config } from "../../config/service-url.config";
import { Observable } from "rxjs/Observable";
import { Filters } from "../../common/utils/filters";

@Injectable()
export class StatsService {

  constructor(public http: HttpService) { }

  public getCompareQuotesConversion(intervalType: string, intervalValue?: number): any {
    return this.http.get(Config.GET_COMPARE_QUOTES_CONVERSION, {
      intervalType,
      intervalValue,
    });
  }

  public getMyFundedAndClosedLoans(intervalType: string, intervalValue?: number): any {
    return this.http.get(Config.GET_MY_CLOSED_LOANS_FUNDED, {
      intervalType,
      intervalValue,
    });
  }
  public getAvgFundedAndClosedLoans(intervalType: string, intervalValue?: number): any {
    return this.http.get(Config.GET_AVG_CLOSED_LOANS_FUNDED, {
      intervalType,
      intervalValue,
    });
  }
  public getMyAdversedAndClosedLoans(intervalType: string, intervalValue?: number): any {
    return this.http.get(Config.GET_MY_CLOSED_LOANS_ADVERSED, {
      intervalType,
      intervalValue,
    });
  }
  public getAvgAdversedAndClosedLoans(intervalType: string, intervalValue?: number): any {
    return this.http.get(Config.GET_AVG_CLOSED_LOANS_ADVERSED, {
      intervalType,
      intervalValue,
    });
  }
  public getMyOriginatedLoans(intervalType: string, intervalValue?: number): any {
    return this.http.get(Config.GET_MY_CLOSED_LOANS_ORIGIN, {
      intervalType,
      intervalValue,
    });
  }
  public getAvgOriginatedLoans(intervalType: string, intervalValue?: number): any {
    return this.http.get(Config.GET_AVG_CLOSED_LOANS_ORIGIN, {
      intervalType,
      intervalValue,
    });
  }
}



