import {
  Injectable,
} from "@angular/core";
import "rxjs/add/operator/map";

import {
  HttpService,
} from "../common/utils/http.service";
import {
  Config,
} from "../config/service-url.config";

@Injectable()
export class QuickQualifierService {

  constructor(public http: HttpService) {}

  public getQualifingResults(params) {
    return this.http.post(Config.QUICK_QUALIFIER_URL, params);
  }
}



