import { Injectable } from "@angular/core";
import "rxjs/add/operator/map";
import { HttpService } from "../common/utils/http.service";
import { Config } from "../config/service-url.config";

@Injectable()
export class ToolsService {
  constructor(public http: HttpService) {}

  public getAdditionalCalculatorResults(params) {
    return this.http.get(Config.ADDITIONAL_PAYMENT_CALCULATOR, params);
  }

}



